.navbar
  width: 210px
  background: #110D59
  color: white
  height: 100%
  display: flex
  flex-direction: column
  row-gap: 20px
  padding: 15px

.navbar
  img
    width: 20px
    filter: invert(100%) sepia(1%) saturate(1813%) hue-rotate(220deg) brightness(118%) contrast(100%)

.elem
  display: flex
  align-items: center
  gap: 20px
  height: 40px
  border-radius: 10px
  padding-left: 10px
  cursor: pointer
  transition: 0.5s
  &:hover
    background: white
    transition: 0.5s
    color: #110D59

.elem:hover img
  filter: invert(6%) sepia(96%) saturate(6822%) hue-rotate(251deg) brightness(53%) contrast(101%)