.mainContainer
  margin: 0 auto
  width: 950px
  height: 1000px
  padding: 15px

.buttonGroup
  display: flex
  border-radius: 10px
  width: 272px
  background-color: white
  box-shadow: 0 0 5px rgba(128, 128, 128, 0.1607843137)
.button
  flex: 1
  padding: 10px 12px
  border: none
  background: none
  color: #666
  font-size: 16px
  border-radius: 10px
  cursor: pointer
  &:hover
    background-color: #ddd
    border-radius: 10px
.selected
  background-color: #0056b3
  color: #fff
  border-radius: 10px
  transition: background-color 0.3s, color 0.3s


.buySell
  width: 85px
  height: 40px
  background-color: #0056b3
  color: white
  border-radius: 10px
  border: none
  cursor: pointer
.flexBlock
  display: flex
  justify-content: space-between

.infoBlock
  background: #1D1872
  height: 180px
  width: 100%
  border-radius: 10px
  margin-top: 30px
  color: white
  padding: 20px

.elemInfo
  display: flex
  margin-top: 20px
  align-items: flex-start

.doneRight
  width: 30px
  position: relative
  top: -6px
.titleInfo
  font-weight: 500
  font-size: 23px

.subTitleInfo
  margin-top: 10px
  opacity: 0.5
  font-size: 15px

.infoRightSide
  margin-left: 10px
.boldInfo
  font-size: 14px

.littleInfo
  margin-top: 7px
  font-size: 12px
  opacity: 0.5

.threeElems
  display: flex
  column-gap: 20px
  margin-top: 10px
  position: relative
.twoSections
  margin-top: 20px
  display: flex
  justify-content: space-between
  > div
    width: 49%
    background: white
    height: 570px
    border-radius: 10px
  box-shadow: 0 0 5px #80808029
.specialBtnElems
  position: absolute
  right: 132px
  bottom: 0
  width: 115px
  height: 40px
  color: #ffffffbf
  cursor: pointer
  border-radius: 10px
  background: #302B7B
  border: none


.cryptoPortfolio
  display: flex
  flex-direction: column
  align-items: center
  padding: 20px

.balanceOverview
  width: 100%
  max-width: 600px
  background: #fff
  border-radius: 10px
  margin-bottom: 20px
  padding: 20px

.balance
  display: flex
  justify-content: space-between
  align-items: center
  margin-bottom: 20px

.viewPortfolio
  background-color: #E7F0F7
  color: #007bff
  border: none
  font-weight: bold
  border-radius: 5px
  padding: 10px 20px
  cursor: pointer

.balanceChart
  width: 100%
  height: 200px

.portfolio
  width: 100%
  max-width: 600px
  background: #fff
  border-radius: 10px
  padding: 20px

.portfolioItems
  display: flex
  flex-direction: column

.portfolioItem
  display: flex
  align-items: center
  margin-bottom: 10px

.coinIcon
  width: 40px
  height: 40px
  border-radius: 50%
  background-color: #f0f0f0
  display: flex
  justify-content: center
  align-items: center
  margin-right: 10px

.coinDetails
  display: flex
  justify-content: space-between
  width: 100%

.coinName
  font-weight: bold

.coinPercentage
  color: #007bff

.coinValue
  color: #333

.coinUsd
  color: #333