header
  width: 950px
  height: 60px
  margin: 0 auto
  display: flex
  justify-content: space-between
  align-items: center
  padding: 20px

.searchBar
  display: flex
  margin-right: 40px

  img
    width: 20px

.notify
  width: 20px
  margin-right: 30px
  cursor: pointer

.secondPart
  display: flex

.person
  width: 20px

.profileInfo
  display: flex
  align-items: center
  cursor: pointer

.circleForPerson
  margin-right: 15px
  background: #EBECEF
  width: 40px
  height: 40px
  display: flex
  justify-content: center
  align-items: center
  border-radius: 50%


.fullName
  width: 150px

.searchInput
  border-radius: 10px
  background: white
  border: none
  color: black
  padding-left: 40px

.searchImg
  position: relative
  right: -30px
  filter: invert(81%) sepia(5%) saturate(185%) hue-rotate(193deg) brightness(85%) contrast(93%)

.partName
  font-weight: 600
  color: #212129
  font-size: 19px
.triangle
  width: 10px
  transform: rotateZ(180deg)
  filter: invert(81%) sepia(5%) saturate(185%) hue-rotate(193deg) brightness(85%) contrast(93%)